.game-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #1a202c;
  color: white;
  padding: 1rem;
}

.text-center {
  text-align: center;
}

.title {
  font-size: 2.25rem;
  font-weight: bold;
  margin-bottom: 1.5rem;
}

.description {
  margin-bottom: 1.5rem;
  font-size: 1.125rem;
}

.start-button {
  background-color: #48bb78;
  color: white;
  font-weight: bold;
  padding: 0.75rem 1.5rem;
  border-radius: 9999px;
  font-size: 1.25rem;
  border: none;
  cursor: pointer;
}

.start-button:hover {
  background-color: #38a169;
}

.high-scores {
  margin-top: 2rem;
}

.high-scores-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.high-scores-list {
  background-color: #2d3748;
  padding: 1rem;
  border-radius: 0.5rem;
}

.high-score-item {
  display: flex;
  justify-content: space-between;
  padding: 0.25rem 0;
}

.game-area {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.score-display {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
  background-color: #2d3748;
  border-radius: 0.5rem;
  margin-bottom: 0.5rem;
}

.score {
  font-size: 1.25rem;
  font-weight: bold;
}

.level {
  font-size: 0.875rem;
}

.play-area {
  flex: 1;
  position: relative;
  overflow: hidden;
  background-color: #2d3748;
  border-radius: 0.5rem;
  border: 2px solid #4a5568;
}

.falling-element {
  position: absolute;
  border-radius: 0.5rem;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.2s;
}

.falling-element.selected {
  background-color: #48bb78;
}

.falling-element:not(.selected) {
  background-color: #4299e1;
}

.falling-element:not(.selected):hover {
  background-color: #3182ce;
}

.element-value {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 0.75rem;
}

.garbage-can {
  position: absolute;
  right: 1rem;
  bottom: 1rem;
  width: 4rem;
  height: 6rem;
  background-color: #4a5568;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  overflow: hidden;
}

.garbage-count {
  position: absolute;
  top: 0.25rem;
  width: 100%;
  text-align: center;
  font-size: 0.75rem;
}

.garbage-item {
  width: 3rem;
  height: 0.75rem;
  background-color: #718096;
  margin-bottom: 0.25rem;
  font-size: 0.75rem;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.word-building-area {
  min-height: 4rem;
  background-color: #2d3748;
  padding: 0.5rem;
  margin-top: 0.5rem;
  border-radius: 0.5rem;
  display: flex;
  flex-direction: column;
}

.current-word {
  display: flex;
  flex-wrap: wrap;
  gap: 0.25rem;
  margin-bottom: 0.5rem;
}

.word-part {
  background-color: #48bb78;
  padding: 0.25rem;
  border-radius: 0.25rem;
  font-size: 0.875rem;
}

.part-value {
  font-size: 0.75rem;
  margin-left: 0.25rem;
}

.word-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.reset-button {
  background-color: #e53e3e;
  color: white;
  font-weight: bold;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  border: none;
  cursor: pointer;
}

.reset-button:hover {
  background-color: #c53030;
}

.word-status {
  flex: 1;
  text-align: center;
}

.valid-word {
  color: #48bb78;
  font-size: 0.875rem;
}

.invalid-word {
  color: #f56565;
  font-size: 0.875rem;
}

.hint {
  background-color: #4a5568;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  font-size: 0.75rem;
}

.name-input-container {
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.name-input {
  padding: 8px 12px;
  font-size: 16px;
  border-radius: 4px;
  border: 2px solid #4a5568;
  background-color: #2d3748;
  color: white;
  margin: 10px 0;
  width: 80%;
  max-width: 300px;
}

.save-score-button {
  background-color: #48bb78;
  color: white;
  font-weight: bold;
  padding: 8px 16px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  margin-top: 10px;
}

.save-score-button:hover {
  background-color: #38a169;
}